export default {
	data() {
		return {
			idx:"-1",
			list:[],
			designArea:'',
			listItem:'',
			flag:false
		};
	},
	props:{
		backImg:{
			type:String
		}
	},
	methods:{
		// 选择类别
		select(index,item){
			this.idx = index;
			this.listItem = item;
			this.$emit('designArea',this.idx,this.designArea,item);
		},
		// 输入框失去焦点
		onBlur(){
			this.flag = false;
			this.$emit('designArea',this.idx,this.designArea,this.listItem);
			if(this.designArea !== ''){
				let regex=/^[0]+/;
				this.designArea=this.designArea.replace(regex,'');
				if(this.designArea == ''){
					this.designArea = 0;
				}
				if(this.designArea.length > 4 ){
					this.designArea = this.designArea.slice(0,4);
				}
				this.designArea = this.designArea + 'm²';
			}
		},
		// 输入框获得焦点
		onFocus(){
			if(this.designArea !== ''){
				this.designArea = this.designArea.slice(0,this.designArea.indexOf('m²'));
			}	
		},
		// 判断用户输入的内容只能输4位
		onInput(e){
			if(this.designArea.length > 4){
				uni.showModal({
				    title: '输入的内容不能超过4位',
					showCancel:false
				});
				this.designArea = this.designArea.slice(0,4);
				this.flag = true;
			}
		}
	},
	mounted(){
		this.$emit('designArea',this.idx,this.designArea);
		let categorys;
		if (uni.getStorageSync("catalogues")) {
			categorys = uni.getStorageSync("catalogues").categorys;
		} else if (this.$store.state.catalogues) {
			categorys = this.$store.state.catalogues.categorys;
		}
		if (categorys && categorys.length > 0) {
			let list= categorys.filter((item)=>{
				return item.type == 1;
			})
			this.list = list;
			// this.list.pop();
		}	
	}
}
